	onReady: function() {
		const { width, height, views, background, radius = 0 } = this.posterData;
		for (let i = 0; i < views.length; i++) {
			console.log('canvas onload views.length:',views.length,' i:',i) ;
		    if (views[i].type === 'image') {
		        //let _img = views[i].url;
		        let _views = views[i];
		        let drawData = {};
				 
		        drawData = {
		            ..._views,
		        };
				console.log('canvas onload image drawData:',drawData,' i:',i) ;
		        this.drawImage(drawData);
				
		    } else if (views[i].type === 'text') {
				 this.drawText(views[i]);
		    } else if (views[i].type === 'rect') {
		        this.drawRect(views[i]);
		    } else if (views[i].type === 'round') {
		        this.drawRound(views[i]);
		    }
		}
		context.draw(true, () => {
		     this.toTempFilePath();
		})
	},
