		_drawRadiusRect: function (params) {
			
		    const {
		        top = 0,
		        left = 0,
		        width = 0,
		        height = 0,
		        radius = 0,
		     } = params;
		    let x = left;
		    let y = top;
		    let w = width;
		    let h = height;
		    // let bgc = background;
		    let r = radius;
		    // let br = radius;
		    context.beginPath();
		    context.moveTo(x + r, y);    // 移动到左上角的点
		    context.lineTo(x + w - r, y);
		    context.arc(x + w - r, y + r, r, 2 * Math.PI * (3 / 4), 2 * Math.PI * (4 / 4));
		    context.lineTo(x + w, y + h - r);
		    context.arc(x + w - r, y + h - r, r, 0, 2 * Math.PI * (1 / 4));
		    context.lineTo(x + r, y + h);
		    context.arc(x + r, y + h - r, r, 2 * Math.PI * (1 / 4), 2 * Math.PI * (2 / 4));
		    context.lineTo((x), (y + r));
		    context.arc(x + r, y + r, r, 2 * Math.PI * (2 / 4), 2 * Math.PI * (3 / 4));
		
		    // context.moveTo(x + r, y);
		    // context.arcTo(x + w, y, x + w, y + h, r);
		    // context.arcTo(x + w, y + h, x, y + h, r);
		    // context.arcTo(x, y + h, x, y, r);
		    // context.arcTo(x, y, x + w, y, r);
		},
		drawImage: function (params) {
			
		    const {
		        type = '',
		        background,
		        top = 0,
		        left = 0,
		        width = 0,
		        height = 0,
		        radius = 0,
		        url = '',
		        sx = 0,
		        sy = 0,
		        ex = 0,
		        ey = 0
		    } = params;
		    let x = left;
		    let y = top;
		    let w = width;
		    let h = height;
		    let r = radius;
		    context.save();
			
		    if (radius) {
		        context.beginPath();
		        
		        // if (radius === parseInt(width / 2)) {
		        //     console.log('圆');
		        //     context.beginPath();
		        //     context.arc(left + radius, top + radius, radius, 0, 2 * Math.PI);
		        //     context.setFillStyle(params.background || '#ffffff')
		        //     context.fill()
		        //     context.clip();
		        //     context.drawImage(url, x, y, w, h);
		        // }
		        // else {
		        // }
		        this._drawRadiusRect(params);
		        context.fill();
		        context.clip();
		    }
		    if ('tailor' in params) {
				if(url){
					 context.drawImage(url, sx, sy, ex, ey, left, top, width, height);
					 console.log('canvas drawImage tailor params:',params);
				}else{
					uni.showModal({
					    title: '图片文件不存在',
					    content: 'draw url:'+ url
					}) 
				}
		    }
		    else {
				if(url){
					 context.drawImage(url, left, top, width, height);
					 console.log('canvas drawImage params:',params);
				}else{
					uni.showModal({
					    title: '图片文件不存在',
					    content: 'draw url:'+ url
					}) 
				}
		       
		    }
		    // this.drawImage(params);
		    
		    context.restore();
		},
		
		drawText: function (params) {
		    let {
		        MaxLineNumber = 2,
		        breakWord = false,
		        color = 'black',
		        content = '',
		        fontSize = 16,
		        top = 0,
		        left = 0,
		        lineHeight = 20,
		        textAlign = 'left',
		        width,
		        bolder = false,
		        textDecoration = 'none'
		    } = params;
		    if (bolder) {
		        top -= 0.3;
		    }
		    // context.save();
		    // context.beginPath();
		    // context.stroke();
		    let _setStyle = () => {
		        // context.save();
		        context.closePath();
		        context.beginPath();
		        context.setTextBaseline('top');
		        context.setFillStyle(color);
		        context.setFontSize(fontSize);
		        context.setTextAlign(textAlign);
		    }
		    _setStyle();
		    if (!breakWord) {
		        context.fillText(content, left, top);
		        this.drawTextLine(left, top, textDecoration, color, fontSize, content);
		    } else {
		        let fillText = '';
		        let fillTop = top;
		        let lineNum = 1;
				let len = 0;  
		        for (let i = 0; i < content.length; i++) {
					len += content[i].match(/[^\x00-\xff]/ig)?2:1   //计算汉字和字母的宽度
		            fillText += [content[i]];
		            // _setStyle();
		            if (context.measureText(fillText).width> width || (len+3)*uni.upx2px(fontSize) > width) { //uni-app APP端measureText bug 规避
		                if (lineNum === MaxLineNumber) {
		                    if (i !== content.length) {
		                        fillText = fillText.substring(0, fillText.length - 1) + '...';
		                        // _setStyle();
		                        context.fillText(fillText, left, fillTop);
		                        this.drawTextLine(left, fillTop, textDecoration, color, fontSize, fillText);
		                        fillText = '';
		                        break;
		                    }
		                }
		                // _setStyle();
		                context.fillText(fillText, left, fillTop);
		                this.drawTextLine(left, fillTop, textDecoration, color, fontSize, fillText);
		                fillText = '';
						len = 0 ;
		                fillTop += lineHeight;
		                lineNum++;
		            }
		        }
		        // _setStyle();
		        context.fillText(fillText, left, fillTop);
		        this.drawTextLine(left, fillTop, textDecoration, color, fontSize, fillText);
		    }
		    // context.draw();
		    if (bolder) {
		        this.drawText({
		            ...params,
		            left: left - 0.3,
		            top: top,
		            bolder: false,
		            textDecoration: 'none'
		        });
		    }
		},
		drawTextLine: function (left, top, textDecoration, color, fontSize, content) {
		    if (textDecoration === 'underline') {
		        this.drawRect({
		            background: color,
		            top: top + fontSize * 1.2,
		            left: left - 1,
		            width: context.measureText(content).width + 2,
		            height: 1
		        });
		    } else if (textDecoration === 'line-through') {
		        this.drawRect({
		            background: color,
		            top: top + fontSize * 0.6,
		            left: left - 1,
		            width: context.measureText(content).width + 2,
		            height: 1
		        });
		    }
		},
		drawRect: function (params) {
		    // console.log(params)
		    const { background, top = 0, left = 0, width = 0, height = 0 } = params
		    context.save();
		    context.setFillStyle(background);
		    if (params.radius) {
		        this._drawRadiusRect(params);
		        context.fill();
		    }
		    else {
		        context.setFillStyle(background);
		        context.fillRect(left, top, width, height);
		    }
		    context.restore();
		},
		
